.body {
  font-family: "Montserrat", sans-serif;
}

label {
  font-family: "Montserrat", sans-serif;
}

div {
  font-family: "Montserrat", sans-serif;
}

.container-sc {
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: 1fr 8fr 1fr;
  gap: 5px;
}

.header-sc {
  background-color: #fca311;
  grid-row: 1;
  grid-column: 1 /-1;
}

.header-sc h1 {
  color: #fff;
  font-weight: 900;
  font-family: "Montserrat", sans-serif;
  margin: 20px;
}

.content-sc {
  grid-row: 2;
  grid-column: 1;
}

.footer {
  background-color: #fca311;
  grid-row: 3;
  grid-column: 1 / -1;
}

.card-income {
  border-radius: 4px;
  position: relative;
  padding: 5px 5px;
  background-color: white;
  margin: 10px 5px;
  height: 700px;
  width: auto;
  box-shadow: 0 2px 5px #ccc;
  text-align: center;
}

.step-guide{
  background-color:#fca311;
  text-align: left;
  margin: 0px;

}

.card-income input {
  margin: 60px 50px;
  width: 70%;
  border: 2px solid #aaa;
  border-radius: 4px;
  margin: 8px 0;
  outline: none;
  padding: 8px;
  box-sizing: border-box;
  transition: 0.3s;
  text-align: center;
  font-size: 20px;
}
.text-input-income{
margin-top:100px;

}

.card-income input:focus {
  border-color: dodgerBlue;
  box-shadow: 0 0 8px 0 dodgerBlue;
}

.card-income p {
  font-size: 20px;
}

.card-income button {
  width: 140px;
  height: 40px;
  border-radius: 4px;
  border: none;
  background: #3b054f;
  color: #fff;
  font-size: 16px;
  text-align: center;
  margin: 30px;
}

.button-demography {
  width: 130px;
  height: 40px;
  padding: 16px 24px;
  border-radius: 4px;
  border: none;
  background: #141414;
  color: #fff;
  font-size: 12px;
  text-align: center;
}

.card-demogrphy {
  display: grid;
  gap: 4px;
}

.container-sc .income-grid {
  display: grid;
  gap: 10px;
}

.container-sc .grid-style-2 {
  display: grid;
  gap: 10px;
  grid-template-columns: 3fr 3fr;
  width: 70%;
}
.container-sc .grid-box {
  display: grid;
  gap: 10px;
  grid-template-columns: 4fr 1fr;
  
}

.step-block{
  display: grid;
  grid-template-columns: auto auto auto auto;
  height: 60%;
}

.header-step{
font-weight: bold;
margin-bottom: 20px;

}

.step-item{
  margin: 20px;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  grid-gap: 5px;
  border-radius: 4px;
  padding: 20px;
  background-color: #f8f9fa;
  margin: 30px 5px;
  height: auto;
  width: auto;
  box-shadow: 0 2px 5px #ccc;
  font-size: 14px;
  text-align: left;
}

.card-2 {
  display: grid;
  grid-template-areas:
    'header header header header'
    'main right right right';
  grid-gap: 5px;
  border-radius: 4px;
  padding: 20px;
  background-color: #f8f9fa;
  margin: 30px 5px;
  height: 140px;
  width: 400px;
  box-shadow: 0 2px 5px #ccc;
  font-size: 14px;
  text-align: left;
}


.item1 { grid-area: header; }
.item2 { grid-area: main; }
.item3 { grid-area: right; }

.header-card-1{
  font-size: 17px;
  margin-bottom: 30px;
  font-weight: bold;


}

.card-2 label {
  margin: 20px 0 0 0;
}

.diff-card {
  margin: 20px 0 0 0;
}

.box {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
}

.box .value.left {
  left: -22px;
}
.box .value.right {
  right: -43px;
}

.slider-container {
  position: relative;
  margin: 20px 0 0 0;
  height: 100%;
  width: 100%;
}
.slider-container .bar {
  position: absolute;
  z-index: 1;
  left: 2px;
  top: 7px;
  width: 100%;
  height: 10px;
  border-radius: 5px;
  background-color: #c6aee7;
  overflow: hidden;
}
.slider-container .bar .fill {
  display: block;
  width: 0;
  height: 100%;
  background-color: #6200ee;
}
.slider-container .slider {
  position: relative;
  z-index: 2;
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  border-radius: 5px;
  outline: none;
  background-color: transparent;
}
.slider-container .slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 25px;
  height: 25px;
  background-color: #6200ee;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  box-shadow: 0 0 0 0 rgba(98, 0, 238, 0.1);
  transition: 0.3s ease-in-out;
}
.slider-container .slider::-webkit-slider-thumb:hover {
  box-shadow: 0 0 0 20px rgba(98, 0, 238, 0.1);
}
.slider-container .slider:active::-webkit-slider-thumb {
  box-shadow: 0 0 0 40px rgba(98, 0, 238, 0.2);
}

